/* src/assets/styles/main.css */

/* General body styles */
body {
  font-family: "Arial", sans-serif;
  background-color: #f8f9fb;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

/* Container styles */
.container {
  display: flex;
  flex-direction: row;
  background-color: #fff;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
}

.left-panel,
.right-panel {
  flex: 1;
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.left-panel {
  background-color: #4ab7bd;
  color: #fff;
  text-align: center;
}

.right-panel {
  background-color: #f0f3f6;
  text-align: center;
}

h2 {
  margin-bottom: 1rem;
  font-size: 1.8rem;
  font-weight: bold;
}

p {
  font-size: 1rem;
  margin-bottom: 2rem;
}

form {
  width: 100%;
  max-width: 300px;
  position: relative;
}

form div {
  position: relative;
  display: flex;
  align-items: center;
}

form div .icon {
  position: absolute;
  left: 10px;
  color: #4ab7bd;
  font-size: 1.2rem;
}

input {
  width: 100%;
  padding: 0.75rem 0.75rem 0.75rem 2.5rem; /* Adjust padding-left for icon */
  margin-bottom: 1rem;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1rem;
  box-sizing: border-box;
}

input:focus {
  border-color: #ddd;
  outline: none;
}

button {
  width: 100%;
  padding: 0.75rem;
  background-color: #007b83;
  border: none;
  border-radius: 5px;
  color: #fff;
  font-size: 1rem;
  cursor: pointer;
  margin-top: 1rem;
}

button:hover {
  background-color: #006a70;
}

a {
  color: #4ab7bd;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.account-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  max-width: 500px;
}

.status-message {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #4ab7bd;
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 5px;
}

.edit-icon {
  cursor: pointer;
  color: #4ab7bd;
  margin-left: 5px;
}
